"use client"

import { useState, useEffect } from "react"
import Link from "next/link"
import { Card } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Button } from "@/components/ui/button"
import { Calendar, Gift, ChevronRight, Loader2 } from "lucide-react"

interface Promotion {
  id: string;
  name: string;
  pointAmount: number;
  totalStock: number;
  startTime: string;
  endTime: string;
  status: "SCHEDULED" | "ACTIVE" | "ENDED";
}

export default function PromotionListPage() {
  const [promotions, setPromotions] = useState<Promotion[]>([])
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    fetch("http://34.50.7.8:3000/v1/promotions?status=ACTIVE&page=0&size=10")
      .then(res => res.json())
      .then(data => {
        if (data.isSuccess) setPromotions(data.result.content)
        setLoading(false)
      })
  }, [])

  if (loading) return <div className="flex h-screen items-center justify-center"><Loader2 className="animate-spin" /></div>

  return (
    <div className="min-h-screen bg-[#f8f9fa] pb-20">
      {/* Header */}
      <div className="bg-white px-6 py-8 border-b border-gray-100">
        <h1 className="text-2xl font-black italic tracking-tighter uppercase">Hot Events</h1>
        <p className="text-sm text-muted-foreground mt-1 font-medium">지금 참여하고 포인트를 획득하세요!</p>
      </div>

      <div className="px-6 py-6 space-y-4">
        {promotions.length > 0 ? promotions.map((p) => (
          <Link href={`/promotions/${p.id}`} key={p.id}>
            <Card className="p-5 border-none shadow-sm rounded-2xl bg-white hover:ring-2 hover:ring-primary/20 transition-all active:scale-[0.98]">
              <div className="flex justify-between items-start mb-3">
                <Badge className="bg-primary/10 text-primary hover:bg-primary/10 border-none rounded-lg px-2 py-1 font-bold italic text-[10px]">
                  {p.status}
                </Badge>
                <div className="flex items-center gap-1 text-primary font-black italic">
                  <Gift className="h-4 w-4" />
                  <span>{p.pointAmount.toLocaleString()}P</span>
                </div>
              </div>
              
              <h3 className="text-lg font-bold text-gray-900 mb-4 line-clamp-1">{p.name}</h3>
              
              <div className="flex items-center justify-between pt-4 border-t border-gray-50">
                <div className="flex items-center gap-2 text-muted-foreground">
                  <Calendar className="h-3.5 w-3.5" />
                  <span className="text-[11px] font-medium">{p.startTime.split('T')[0]} 시작</span>
                </div>
                <ChevronRight className="h-5 w-5 text-gray-300" />
              </div>
            </Card>
          </Link>
        )) : (
          <div className="py-20 text-center text-muted-foreground font-medium italic">진행 중인 이벤트가 없습니다.</div>
        )}
      </div>
    </div>
  )
}